@media (width < 1520px){
  .mainPage{
    > section:not(.youthArea){padding-inline: 30px;}
  }
  [class$="Page"]:not(.mainPage) {
    .banner + *{padding-inline: 30px;}
    .banner {
      > ul{padding-inline: 30px; gap: 15px; font-size: 16px
        ;
        > li::after{width: 95%;}
      }
    }
  }
}

/* mainPage */
@media (width > 1120px) {
  .mainPage{  
    /* League & Match  */
    .matchArea { color: var(--color--white); 
      > h2 { position: relative; display: flex; align-items: center; justify-content: center; margin-bottom: 70px; height: 50px; 
        > span { z-index: 0; padding-inline: 50px; background-color: var(--color--black); } 
        &::before { width: 100%; height: 1px; left: 0; top: 50%; transform: translate(0%, -50%); background-color: var(--color--white); content: ''; position: absolute; z-index: 0; } 
    } 
  }
  
  /* Market */
  .marketArea { color: var(--color--white);
    h2 { text-align: center; } 
    > a { max-width: 250px; font-size: 18px; line-height: 55px; padding-left: 40px; margin-inline: auto; margin-bottom: 70px; display: block; position: relative; box-sizing: border-box; border: 1px solid var(--color--white); border-radius: 30px; } 
    > ul { width: 100%; display: grid; gap: 30px; grid-template-columns: repeat(3, 1fr); grid-template-rows: repeat(2,1fr); 
      > li:hover { border: 5px solid var(--color--blue); } 
      > li { overflow: hidden; background-color: var(--color--blodGray); border: 5px solid var(--color--blodGray); border-radius: 30px; 
        > a { max-width: 455px; aspect-ratio: 455/377; display: flex; align-items: center; justify-content: flex-start; flex-direction: column; text-align: center; } 
        > a > p { width: 100%; line-height: 78px; font-size: 18px; background-color: var(--color--gray); } 
      } 
      > li:nth-child(3) { grid-column: 3/4; grid-row: 1/3; position: relative; display: flex; align-items: center; justify-content: center; 
        > a > p { bottom: 0; position: absolute; } 
      } 
    } 
  }
  
  /* Feed */
  .feedArea {color: var(--color--white);}
  
  /* Gallery */
  .galleryArea { 
    > h2 {  height: 1px;  text-indent: -9999px;} 
    > ul {  width: 100%;  margin-bottom: 145px;  display: grid;  grid-template-columns: 1fr 1fr;  grid-template-rows: 430px 675px;  grid-gap: 70px 2.78%;  text-align: center; 
      > li { 
        > h3 { margin-bottom: 48px; } 
        > a { display: block;  width: 100%;  aspect-ratio: 525/325;  border-radius: 20px;  text-indent: -9999px; } 
        &:nth-child(1) > a { background: url(../../public/images/main/Photo_Section_01.png) no-repeat center / cover;  } 
        &:nth-child(2) > a { background: url(../../public/images/main/magazine.png.jpg) no-repeat center / cover; } 
        &:nth-child(3) {  grid-column: 1/3; 
          > iframe {    width: 100%;    aspect-ratio: 1080 / 591;    border-radius: 20px;  } 
        } 
      } 
    } 
  }
  
  /* Academy  */
  .academyArea { 
    background: url(../../public/images/main/academy.BackGround_Img.png) no-repeat center / auto;  
    > div {  position: relative;  
      > h2 { line-height: 60px;  display: inline; 
        &::after { content: ""; display: inline-block; width: 52.31%; height: 18px; margin-inline: 4.63%; border-top: 1px solid var(--color--white);} 
      }     
      > p {  font-size: 18px;  font-weight: 200;  line-height: 30px;  top: 70px;  right: 0;  position: absolute;  text-align: right;  color: var(--color--white); }   
      > a {  font-size: 20px;  width: 44.44%;  line-height: 78px;  text-indent: 80px;  display: block;  margin-left: auto;  position: relative;  box-sizing: border-box;  border-radius: 10px;  border: 1px solid var(--color--white);  
        &:hover { background-color: var(--color--blue);  border: 1px solid var(--color--blue); } 
        &:nth-of-type(1) { margin-top: 200px; margin-bottom: 25px;} 
      } 
    } 
  }
  
  /* Event */
  .eventArea { 
    > ul { padding-inline: 50px; display: grid; grid-template-columns: 44.44% 44.44%; grid-auto-rows: 330px 130px 70px 260px 200px; gap: 0px 4.85%; 
      > li { border-radius: 20px; background-color: var(--color--blodGray); 
        &:nth-of-type(1) { grid-column: 2/3; grid-row: 1/3; } 
        &:nth-of-type(2) { grid-column: 1/2; grid-row: 2/5; } 
        &:nth-of-type(3) { grid-column: 2/3; grid-row: 4/6; } 
        &:nth-of-type(1) > a { background: url(../../public/images/main/event_01.png) no-repeat center / contain; } 
        &:nth-of-type(2) > a { background: url(../../public/images/main/event_02.png) no-repeat center / contain; } 
        &:nth-of-type(3) > a { background: url(../../public/images/main/event_03.png) no-repeat center / contain; } 
        > a { width: 100%; height: 100%; padding-top: 68px; padding-bottom: 50px; text-indent: 35px; display: flex; flex-direction: column; justify-content: space-between; box-sizing: border-box; 
          > strong { font-size: 24px; font-weight: 500; color: var(--color--blue); } 
          > p { width: 100%; font-size: 18px; font-weight: 300; position: relative; color: var(--color--white); } 
        } 
      } 
    } 
  }
  
  /* Youth */
  .youthArea { 
    padding-top: 300px; padding-bottom: 148px; background: url(../../public/images/main/iufcYouthBackGround_img.png) no-repeat bottom / cover; text-align: center; 
    h2::before { content: ""; display: block; width: 100%; height: 36px; margin-bottom: 50px; background: url(../../public/images/main/iufcYouth_img.png) no-repeat center / auto; } 
    > h2 { margin-bottom: 92px; } 
    > p { font-size: 18px; line-height: 30px; margin-bottom: 44px; } 
    > a { font-size: 20px; max-width: 480px; line-height: 80px; border-radius: 15px; display: block; position: relative; background-color: #4040408f; margin-inline: auto; } 
  }
  
  /* Sponsor */
  .sponsorArea { 
    margin-block: 150px; color: var(--color--white); text-align: center; 
    > * { margin-inline: auto; } 
    a { display: block; 
      &:nth-of-type(1) { max-width: 175px; border: 1px solid var(--color--white); padding-inline: 44px; padding-block: 17px; border-radius: 100px; font-size: 18px; font-weight: 500; box-sizing: border-box; margin-bottom: 48px; } 
      &:not(:nth-of-type(1)) { box-sizing: border-box; border: 1px solid var(--color--white); max-width: 480px; line-height: 67px; border-radius: 10px; } 
      &:nth-of-type(2) { margin-bottom: 25px; } 
      &:hover:not(:nth-of-type(1)) { background-color: var(--color--blue); border: 1px solid var(--color--blue); } 
    } 
    > h2 { margin-bottom: 103px; } 
    > ul {
      display: flex; max-width: 1290px; align-items: center; justify-content: space-between; gap: 10.39%; margin-bottom: 100px; 
      > li { height: 58px; width: 20.93%; text-indent: -9999px; 
        &:nth-child(1) { background: url(../../public/images/main/logo_sponsor_1.png) no-repeat center / contain; } 
        &:nth-child(2) { background: url(../../public/images/main/logo_sponsor_2.png) no-repeat center / contain; } 
        &:nth-child(3) { background: url(../../public/images/main/logo_sponsor_3.png) no-repeat center / contain; } 
        &:nth-child(4) { background: url(../../public/images/main/logo_sponsor_4.png) no-repeat center / contain; } 
      } 
    } 
  }
  
}


/* Sub page start ! */

/* Club */
.clubPage {
  /* Introduction */
    .introductionArea{
      > img { display: block; width: 100%;}
  };

  /* Vision */
  .visionArea { color: var(--color--white);
    .mark-bg-blue{position: relative;}
    .mark-bg-blue::after{background-color: var(--color--blue);position: absolute; height: 20px; content: ''; display: block; width: 100%; transform: translate(0,-50%); top: 75%; z-index: -1;}
    > *:not(:last-child, h3){ margin-bottom: 150px;}
    > img{  display:block; width:100%;}
    h3 { font-size: 34px; font-weight: 600;}
    > h3{ margin-bottom: 70px;}
    > ol { display:flex; justify-content:space-between; width:100%; gap:2.08%; counter-reset: listNumber; 
      > li{ width:31.67%; aspect-ratio:1/1; padding-top:35px; border-radius:20px; counter-increment: listNumber; background-color:var(--color--blodGray); display:flex; flex-direction:columns; align-items:flex-start; justify-content:center; flex-wrap:wrap; 
        > p { width:100%; font-size:18px; line-height:30px; text-align:center; padding-bottom:28px; border-bottom:solid 1px var(--color--gray);}
        > p::before{ content: '0'counter(listNumber); display: block;}
        > div{ display:flex; align-items:center; justify-content:center;}
      }
    }
    > div:nth-of-type(1){ width:100%; display:flex; align-items:center; justify-content:space-between;
      > img { width: 50%;}
      > div { width:44%; font-size:34px;  display:flex; flex-direction:column; justify-content:center; font-weight:600; 
        > p {line-height: 45px; position: relative; z-index:0; display: inline-block;} 
        > p:nth-child(1){margin-bottom: 50px;}
      }
    }
    > div:nth-of-type(2) {width: 100%;}
    > div:not(:nth-of-type(1)){
      > h3 { margin-bottom: 40px;position: relative; z-index: 1;}
      > p {color: var(--color--blue); margin-bottom: 70px;}
      > p::before { width: 14px; height:14px; margin-right:2px; display:inline-block; background-color:var(--color--blue);  content:''; border-radius:100%;}
      > ol {counter-reset: listNumber; border-top:2px solid var(--color--gray); border-bottom:2px solid  var(--color--gray);
        > li {width: 100%; height:107px; counter-increment: listNumber; display:flex; align-items:center;
          &:not(:last-of-type){border-bottom: solid 1px var(--color--gray);}
          > div{ display:flex; align-items:center; width: 40%;
            &::before{content:'0'counter(listNumber); font-size:34px;  margin-right:8px; color:var(--color--gray); font-weight:900;}
            > mark {margin-right: 14px; line-height: 40px; white-space: nowrap;}
            > strong {font-size: 20px; color: var(--color--blue); margin-right: 10px;}
          }
          > p { font-weight:300; line-height:25px; position:relative;
            &::before{top:0; left:-7px; position:absolute; content:'-';}
            > b{font-weight:600;}
          }
        }
      }
    } 
  }
  
  /* Organization */
  .organizationArea {
    > mark.bg-color-blue {  line-height:33px; margin-bottom:35px;}
    > h3 { font-size:34px; margin-bottom:50px;}
    > ul { font-size: 20px; width:100%; gap:25px; margin-bottom:145px; display:flex; align-items:center; justify-content:space-between; flex-wrap:wrap;
      > li{flex-grow: 1;   padding-inline: 35px; padding-block: 33px; border-radius:30px; border:1px solid var(--color--white); align-items: center;display:flex;background: rgb(13,14,15);background: radial-gradient(circle, rgba(13,14,15,1) 48%, rgba(26,29,30,1) 77%);
        > strong { margin-right:60px; color:var(--color--blue);}
        > p { line-height:25px; }
        &:last-of-type{align-items: flex-start;}
      }
    }
    > p { width: 100%; font-size:18px; font-weight:300; line-height:25px;
      > span{padding-top: 50px;  display: block;  color: var(--color--gray);}
      &:nth-of-type(1){margin-bottom: 70px;}
    }
    img{display: block; width: 100%;}
  } 
  
  /* Emblem */
  .emblemArea {color: var(--color--white); width: 100%;
    > figure { width:100%; display:flex; justify-content:space-between; align-items:flex-start; flex-wrap: wrap;
      figcaption {width: 47.25%;
        > mark.bg-color-blue {  padding-block:7.5px; margin-bottom:36px;}
        > h3 { font-size:34px; font-weight:600; margin-bottom:50px;}
        > p { font-size:18px;  font-weight:400;  line-height:33px;  margin-bottom:50px;
          > span{ display: block; margin-bottom: 28px;}}
          > div { width: 100%;display: flex; gap: 20px;}
        }
        &:not(:last-of-type){margin-bottom: 150px;}
        > img{width: 47.25% ;}
      }
      button { padding-inline:20px 50px; line-height:54px; font-size:18px; box-sizing:border-box; border:1px solid white; border-radius:10px; position:relative; cursor: pointer;
        &::after {width:18px; height:16px; right: 2.5%; top:50%; transform:translate(-50% , -50%); background-image:url('../../public/images/club/downloadicon01.png'); content:''; position:absolute; display:inline;}
      }
    }
    
    /* History */
    .historyArea{
      > h2 { font-size: 70px; font-weight: 600; margin-bottom: 70px;
        > span { color: var(--color--blue);}
      }
      > img {display: block; width: 100%;}
      > ol{ width: 100%; position: relative;
        &::after {content: ''; height: 100%; width: 1px; background-color: var(--color--gray); position: absolute; display: block; top: 0; left: 225px;}
        > li{padding-top: 70px; width: 100%; display: flex;  justify-content: flex-start; align-items:flex-start ;
          > span { width: 15.60%; margin-right: 150px; font-size: 70px; font-weight: 600; position: relative;
            &::after{content: ''; display: block; width: 10px; height: 10px; background-color: var(--color--blue); position: absolute; border-radius: 100%; left: 225px; z-index: 1; top:50%; transform: translate(-50%,-50%); }}
            > ul { font-size: 18px; line-height:25px;
              > * {display: block;}
              > strong {font-size: 34px ; margin-bottom: 50px;padding-top: 25px;}
              > li:not(:last-of-type){margin-bottom: 35px}
          }
        }
      }
    }
    
    /* Greeting */
    .greetingArea{
      > h2 {margin-bottom: 150px; font-size: 60px; }
      > ul {width: 100%; margin-bottom: 300px; display: flex; justify-content: space-between; flex-wrap: wrap; 
        > li {width: 46.32%; height: 484px; align-items: flex-start; justify-content: space-between; display: flex; flex-direction: column; background-repeat: no-repeat; background-position: right 0 bottom 0; background-size:auto;
          &:nth-of-type(1) {background-image: url('../../public/images/club/bg_club_greeting_owner.png');}
          &:nth-of-type(2) {background-image: url('../../public/images/club/bg_club_greeting_ceo.png');}
          > p{font-size: 34px; line-height: 40px; font-weight: 600;
            > span{font-size: 24px; margin-bottom: 32px; display: block;  color: var(--color--gray); font-weight: 600;}} 
            > button { line-height: 56px; font-size: 18px ; padding-inline: 45px ; box-sizing: border-box; border: 1px solid var(--color--white); border-radius: 5px;cursor: pointer; color: #bbbbbb;
              &::after{width:21px; height: 11px; margin-left: 15px; content: ''; display: inline-block; background: url('../../public/images/club/arrow-1.png') no-repeat center / cover; }
            &.active::after{background-image: url('../../public/images/club/arrow.png');}
          }
        }
      }
    > div {
      &.active{display: none;}
      > h3 {font-size: 60px; line-height: 75px; margin-bottom: 145px;}
      > div {margin-bottom: 135px; display: flex; align-items: flex-start;width: 100%;
        > img { width: 45%;}
        > div {width: 45%;
          > span{font-size: 24px; margin-bottom: 33px; color: var(--color--blue); display: block; font-weight: 600;}
          p{
            &:nth-of-type(1){ color: var(--color--gray); font-size: 34px; font-weight: 600; display: block; margin-bottom: 140px;}
            > span{ color: var(--color--white);}
            &:not(:first-of-type){line-height:25px;}
            &:not(:first-of-type,:last-of-type){margin-bottom: 25px;}
          }
          > strong {font-size: 24px; margin-bottom: 25px; font-weight: 400; display: block; }
        }
      }
      > p {line-height: 25px; text-align: center;
        &:not(:last-of-type){margin-bottom: 25px;}
      }
    }
  }
  
  /* stadiumArea */
  .stadiumArea{
    > h3{font-size: 24px; color: var(--color--blue); margin-bottom: 30px;}
    p {font-size: 18px; line-height: 25px; margin-bottom: 70px;}
    > img {display: block;margin-bottom: 70px; width: 100%;}
    > ul {display: flex; width: 100%; justify-content: space-between;
      > li {width: 31.67%;
        > strong{font-size: 24px; font-weight: 600; margin-bottom: 30px; padding-bottom: 30px; border-bottom: 1px solid var(--color--gray); display: block; 
          &::before{content: ''; display: inline-block; width: 34px; height: 30px; background-repeat: no-repeat; background-position: center; background-size: auto; margin-right: 15px; }
        }
        &:nth-of-type(1) > strong::before{background-image: url('../../public/images/club/stadium-icon-02.png');}
        &:nth-of-type(2) > strong::before{background-image: url('../../public/images/club/stadium-icon-01.png');}
        &:nth-of-type(3) > strong::before{background-image: url('../../public/images/club/stadium-icon-03.png');}
      }
    }
    > a { padding-left: 50px; font-size: 18px; width: 75%; height: 165px; box-sizing: border-box; border: 1px solid var(--color--gray); margin-left: auto;  display: flex;align-items: center; cursor: pointer; position: relative;
      > span{display: flex; align-items: center;
        &::before{height: 35px; width: 23px; content:url('../../public/images/club/map-btn-01.png'); display: inline-block; margin-right: 15px;}
        &::after{content: url('../../public/images/club/kakaomap.png'); display: inline-block; position: absolute; right: 50px;}
      }
    }
  } 
  
  /* parkingArea */
  .parkingArea {
    h3 {font-size: 24px; margin-bottom: 30px;&:nth-of-type(1) { color: var(--color--blue); }}
    > p { font-size: 18px; line-height: 25px; margin-bottom: 70px; }
    > img { display: block; width: 100%; }
    > ul:nth-of-type(1) {
      gap: 2%; width: 100%; margin-bottom: 150px; flex-wrap: wrap; display: flex; justify-content: flex-start; align-items: center;
      > li {
        width: 31.67%; font-weight: 600;
        &:nth-of-type(1) { color: #b94a15; > p::before { content: url('../../public/images/club/parking_icon_01.jpg'); } }
        &:nth-of-type(2) { color: #d18302; > p::before { content: url('../../public/images/club/parking_icon_02.jpg'); } }
        &:nth-of-type(3) { color: #544fa3; > p::before { content: url('../../public/images/club/parking_icon_03.jpg'); } }
        &:nth-of-type(4) { color: #cb3117; font-weight: 400; > p::before { content: url('../../public/images/club/parking_icon_04.jpg'); } }
        &:nth-of-type(5) { color: #2d2792; > p::before { content: url('../../public/images/club/parking_icon_05.jpg'); } }
        > p::before { width: 100%; padding-top: 68px; padding-bottom: 34px; margin-bottom: 30px; display: block; border-bottom: 1px solid var(--color--gray); }}
      }
      > ul:nth-of-type(2) {
        display: flex; width: 100%; align-items: flex-start; justify-content: space-between; margin-bottom: 150px;
        > li {
          width: 31.67%;
          > img { display: block; width: 100%; margin-bottom: 35px; }
          > strong {
            width: 100%; font-size: 24px; font-weight: 600; height: 45px; margin-bottom: 34px; gap: 14px; display: flex; align-items: center;
            &::before { width: 45px; height: 45px; }
            &:nth-of-type(1) { color: #544fa3; &::before { content: url('../../public/images/club/parking_icon_06.jpg'); display: inline-block; } }
            &:nth-of-type(2), &:nth-of-type(3) { color: #d55518; &::before { content: url('../../public/images/club/parking_icon_07.jpg'); display: inline-block; } }
            &:nth-of-type(3) { &::before { content: url('../../public/images/club/parking_icon_08.jpg'); } }
          }
          > p { line-height: 25px; font-size: 16px; font-weight: 400; }}
        }
        > div {
          font-size: 18px; display: flex; align-items: center; justify-content: space-between; padding-inline: 50px; padding-block: 30px; border: 1px solid var(--color--gray); box-sizing: border-box;
          > ul > li {
        display: flex;
        &:not(:last-of-type) { margin-bottom: 20px; }
        &::before { content: url('../../public/images/club/parking_bus.png'); display: inline-block; margin-right: 15px; }
        &:nth-last-of-type(1)::before { content: url('../../public/images/club/parking_train.png'); }}
        > div {
          display: flex; gap: 50px; text-align: center;
          > span {
            > img { margin-bottom: 20px; }
            > p { line-height: 25px; font-weight: 400; > span { color: var(--color--gray); } }
          }}
        }
      }
      
      
    }  
  
  
  /* player */

  .playerPage{
      /* CoachingStaff */
      .coachingStaffArea {
      > h2 {height: 0.1px; text-indent: -9999px;} 
      > div { width: 100%; gap: 10%; display: flex;align-items: flex-start; margin-bottom: 150px; flex-wrap: wrap;
        > * {width: 41.61%;}
        > div { font-size: 24px;
          > button {width: 50%; height: 62px; cursor: pointer; box-sizing: border-box; border: 1px solid var(--color--gray);
            &.active{background-color: var(--color--gray);}
          }
        }
        .coachProfile,.coachBriefHistory {padding-top: 150px;}
        .coachProfile{
          > span {margin-bottom: 25px; color: var(--color--blue); display: block;}
          > strong{line-height: 35px; display: block; margin-bottom: 35px;
            > span{ color: var(--color--gray);}
          }
          > ul{
            > li{font-size: 18px; line-height: 75px;display: flex; font-weight: 400;
            > span{width: 90px; margin-right: 50px; color: var(--color--gray);}
          }
        } 
      } 
      .coachBriefHistory{
        > strong {font-size: 34px; line-height: 45px; margin-bottom: 70px; display: block;} 
        > ul {font-size: 18px; line-height: 40px; font-weight: 400; 
          > li {display: flex; gap: 30px; 
            > span {width:90px; color: var(--color--gray);}
            > div > p > span {width: 170px; display: inline-block;}
          }
        }
      } 
    }
    > h3 {font-size: 24px; margin-bottom: 70px;}
    > ul {width: 100%; justify-content: space-evenly; display: flex;  flex-wrap: wrap;  align-items: center; margin-bottom: 50px;
      > li {flex-direction: column; padding-block: 75px; width: 30%; box-sizing: border-box; flex-grow: 0;display: flex; align-items: center; justify-content: center; text-align: center; position: relative;
        > img {width: 100%; display: block;}
        &:hover > img {filter: blur(5px); -webkit-filter: blur(5px);}
