        > strong {font-weight: 900; font-size: 34px;}  
      }
      > div {display: flex; align-items: center; gap: 18px; justify-content: flex-start;
        > a {border: 1px solid var(--color--white); line-height: 36px; padding-inline: 20px; border-radius: 20px;
          &::after{content: ''; width: 15px; height: 10px; display: inline-block; background: url(../../public/images/ticketMembership/arrow.png); margin-left: 10px;}          
        }
      }
    }
  }
  
  /* membership */
  .buyMembershipArea { 
    &:last-child{margin-bottom: 150px;}
    > h2 {margin-bottom: 50px;}
    > p {font-size: 34px; font-weight: 600;text-align: center; margin-bottom: 70px;}
    > div {margin-bottom: 70px;  text-align: center;
      > a {background-color: var(--color--blue); line-height: 85px; padding-inline: 70px; display: inline-block; font-size: 40px; font-weight: 900;}
    }
    > img {display: block; width: 100%;}
  }
  
  /* FAQ */
  .frequentlyQandAArea {
    > h2 {font-size: 40px; font-size: 600; color: var(--color--white); text-align: left; margin-bottom: 35px;}
    > details {border: 1px solid var(--color--gray);
      &:not(:nth-last-of-type(1)){border-bottom: 0;}
      > summary {line-height: 113px; list-style-type: none; color: var(--color--gray); padding-inline: 35px; cursor: pointer; 
        &.active{color: var(--color--blue);}
        &::before {content: 'Q.'; margin-right: 20px;}
      }
      > div { border-top: 1px solid var(--color--gray); padding-block: 50px; line-height: 25px;  background-color: var(--color--blodGray); position: relative; width: 100%;
        > p {padding-left: 75px;  font-size: 15x; }
        &::before {content: 'A.'; margin-right: 20px; display: inline; position: absolute; left: 35px;}   
      }
    }
  }
}
}


/* adminPage */
.adminPage {
  > .loginArea { max-width: 580px;  margin-inline: auto; height: 100vh;
      h2 {margin-bottom: 30px; font-size: 34px; text-align: center; padding-top: calc((100vh - 432px) / 2);}
      > form { max-width: 570px; width: 100%; margin: 0 auto; font-size: 18px; margin-bottom: 35px;
          > * {width: 100%; display: block;}
          > input , button {border-radius: 10px;}
          > label {color: var(--color--gray);margin-bottom: 18px;}
          > input {background-color: #262626; width: calc(100% - 40px); margin-bottom: 50px; line-height: 64px; padding-inline: 20px; color: white; font-weight: 300;}
          > button {line-height: 64px; background-color: var(--color--blue); cursor: pointer;}
      }
  }
   .usersArea{ height: 1000px; width: 100%; border-left: 1px solid #262626; padding-inline: 30px;
